
:root {
    --light: #af96b3;
    --dark: #9c8ca9;
    --verydark: #1c1d30
}
body {
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(https://images.assetsdelivery.com/compings_v2/smallsmiles/smallsmiles1803/smallsmiles180300027.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-color: var(--light);
    color: ghostwhite;
    background-blend-mode: multiply;
}
.app {
    margin-left: auto;
    margin-right: auto;
}
.header {
    background-color: var(--light);
    padding: 1rem;
    margin: 1rem;
    width: 90%;
    border: 1px solid ghostwhite;
    border-radius: 20px;
}
.formCSS {
    display: flex;
    /* flex-direction: column; */
    height: 3rem;
    padding: 1rem
}
li {
    list-style-type: none;
}
h1 {
    color: ghostwhite ;
}
.add-edit {
    background-color: var(--light);
    padding: 1rem;
    width: 50%;
    border: 1px solid ghostwhite;
    border-radius: 20px;
}
.add-edit-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: var(--dark);
    /* padding: 1rem; */
    /* width: 75%; */
    border: 1px solid ghostwhite;
    border-radius: 20px;
}
.item {
    border: 1px solid ghostwhite;
    border-radius: 20px;
    margin: 20px auto;
    background-color: var(--verydark);
    width: 90%
}
button {
    margin: 10px;
}
.link {
    text-decoration: none;
}